.formSummaryError {
    border: 1px solid red;
    color: red;
    background-color: #FFFFFF;
    width: 100%;
    text-align: center;
    margin-top: 20px;
    padding: 10px;
    font-weight: bold;
    border-radius: 5px;
}

.blockForm{
    background-color: transparent;
    width: 250px;
    margin: 20px  auto;
    text-align: center;
   align-self: center;
    padding: 20px;
    -webkit-box-shadow: -1px 2px 7px 9px rgba(3,113, 3, 0.8);
    -moz-box-shadow: -1px 2px 7px 9px rgba(3,113, 3, 0.8);
    box-shadow: -1px 2px 7px 9px rgba(3,113, 3, 0.8);
}
.blockForm h1{
font-size: 1.5em;
    color: #ffffff;
}
.loginForm{
    width: 100%;
    margin-top: 20px;
}
.loginFormInput{
    margin: 20px auto;
    width: 100%;
}
.loginFormInput input{
    width: 100%;
    padding: 10px;
    background-color: #FFFFFF;
    border: 2px solid #037103;
    border-radius: 5px;
}

.loginFormRememberMe input{
    margin-right:10px;
}
.loginFormRememberMe{
    color: var(--main-color-text);
    display: flex;
    justify-content: center;
}
.loginFormRememberMe input:checked{


    background-color: #037103;
    border-radius: 2px;
    display: block;
}
.loginFormButton{
    margin: 20px auto;
    text-align: center;
}
.wrapperBlockForm{
    width: 100%;
    display: flex;
}