
.form{
    width: 100%;
    margin: 20px auto;
    text-align: center;
    color: var(--main-color-text) ;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;

}
.form__formSearch{
display: flex;
}
.form label, input, select{
    margin-right: 10px;
}
.form select, input{
    height: 24px;
    outline: none;
    border: 1px solid #1D1628;
}
.form__button{
    background: url("../../../img/green.png") 0 0 no-repeat;
    color: #ffffff;
    font-size: 12px;
    text-align: center;
    font-weight: 600;
    padding: 6px 12px;
    border-radius: 3px;
    text-transform: uppercase;
    cursor: pointer;
    border: none;
    outline: none;
    transition: all 0.5s ease 0s;
}
.form__button:hover{
    background-image: url("../../../img/fon_hover_btn.png");
}
.form__button:active{
    -webkit-box-shadow: 1px 0 22px 7px rgba(34, 60, 80, 0.2) inset;
    -moz-box-shadow: 1px 0 22px 7px rgba(34, 60, 80, 0.2) inset;
    box-shadow: 1px 0 22px 7px rgba(34, 60, 80, 0.2) inset;
}


@media screen and (max-width: 700px) {
    .form__formSearch{
        flex-flow:column;
    }
    .form label, input, select{
        margin-bottom: 10px;
        margin-right: 0;
    }
}
