.dialog{
    width: 100%;
    max-width: 1280px;
    box-sizing: border-box;
    padding: 20px;
    margin: 20px auto;
    display: flex;
    background-color: #FFFFFF;
}
.usersPage{
    width: 100%;
    max-width: 1280px;
    box-sizing: border-box;
    padding: 50px;
    margin: 0 auto;
    text-align: center;
}
.dialogs:after {
    content: "."; /* добавить содержимое: "." */
    display: block;  /* сделать блоком, т.к. inline не может иметь clear */
    clear: both;  /* с обеих сторон clear */
    visibility: hidden; /* сделать невидимым, зачем нам точка внизу? */
    height: 0;  /* сделать высоту 0, чтобы не занимал место */
}
.dialog img{
    width: 70px;
    border-radius: 50%;
}
.blockFoto{
    margin-right: 20px;
}
.blockFoto button{
    width: 70px;

    border: none;
    border-radius: 5px;
    padding: 5px;
    color:#FFFFFF;
    outline: none;
    font-size: 10px;
    transition: all 0.5s ease 0s;
}
.blockFoto button:hover{
    background-color:#769B00!important;
}
.blockName .status{
    display: block;
    margin-top: 15px;
    font-weight: bold;
}
.blockName b{
   color: var(--main-color-text);
    font-weight: bold;
}
.counterUsers{
    width: 100%;
    border: 1px solid #1D1628;
    font-size: 13px;
    line-height: 50px;
    font-weight: 500;
    text-transform: uppercase;
    position: relative;
    background-color: #fff;
    padding-left: 10px;
    padding-right: 10px;
    box-sizing: border-box;
    float: left;
    margin-bottom: 22px;
}
.counterUsers .text{
    float: left;
}
.counterUsers .numbers{
    float: right;
    background: #037103;
    color: #fff !important;
    font-size: 14px;
    font-weight: bold;
    padding: 0 5px;
}

button:disabled{
    background-color: #E1D6C2;
}
@media screen and (max-width: 524px) {
    .usersPage{
        padding: 5px;
    }
}