.textareaAddMess{
    width: 100%;
    box-sizing: border-box;
}
.input{
    width: 100%;
    box-sizing: border-box;
}
.error input,
.error textarea {
    border: 1px solid #FF0000;
    width: 100%;
}
.spanError{
    display: block;
    font-size: 12px;
    font-weight: bold;
    color: #FF0000;
    padding: 10px;
    background-color: #ffffff;
    border: 1px solid #FF0000;
    width: 100%;
    box-sizing: border-box;
}
input {outline: none;}
input:-webkit-autofill {
    -webkit-box-shadow: inset 0 0 0 50px #fff !important; /* Цвет фона */
}