.block {
    padding: 10px;
    box-sizing: border-box;
    margin-top: 10px;

    width: 100%;
}
.block h3{
    margin: 20px;
    font-size: 1.7em;
    text-align: center;
    font-weight: bold;
}
.input_text{
    width: 75%;
    min-height: 50px;
    color: gainsboro;
    border: 1px solid #9160CF;
    padding-left: 10px;

}
.input_text::-moz-placeholder{
    color: gainsboro;
    opacity: 1;
    transition: opacity .3s ease;
}
.input_text:-moz-placeholder{
    color: gainsboro;
    opacity: 1;
    transition: opacity .3s ease;
}
.input_text::-webkit-input-placeholder{
    color: gainsboro;
    opacity: 1;
    transition: opacity .3s ease;
}
.input_text:focus::-moz-placeholder{
    opacity: 0;
}
.input_text:focus:-moz-placeholder{
    opacity: 0;
}
.input_text:focus::-webkit-input-placeholder{
    opacity: 0;
}
.input_text:focus{
    outline: none;
}

.reset_properties{
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}
.formAddPost{
    margin: 20px;

}

@media screen and  (max-width: 576px){
    .block h3{
        text-align: center;
    }
}
