.currentPage{
    font-weight: 900;
    background-color:#037103;
    color: white;
}
.block{
    font-weight: bold;
    width: 100%;
}
.block button{
    background: url("../../img/green.png") 0 0 no-repeat;
    color: #ffffff;
    border: none;
    outline: none;
    border-radius: 2px;
    transition: all 0.5s ease 0s;
}
.block button:hover{
    background-image: url("../../img/fon_hover_btn.png");
    border: none;
    outline: none;
    border-radius: 2px;
}
.block span{
    display: inline-block;
    cursor: pointer;
    padding: 1px;
}