
.block{
    width: 30%;
    height: 100%;
    display: flex;
    font-size: 13px;
}
.itemsSize{
    width: 33.33333333333333%;
    min-width: 100px;
    height: 100%;
    box-sizing: border-box;
    font-weight: 700;
    display: flex;
    align-items: center;
    justify-content: space-around;
    text-align: center;
}
.link{
    text-decoration: none;
    color: #ffffff;
    text-transform: uppercase;
    padding: 8px 15px;
    border-radius: 3px;
    transition: all 0.5s ease 0s;
}
.link:hover{
    background-color:var(--main-color-hover);
   color: #ffffff;
}
.item1, .item3{
    /*background-color: var(--main-color-light-violet);*/
    background-image: url("../../../img/green.png");
}
.item2{
    /*background-color: var(--main-color-pastel-magenta);*/
    background-image: url("../../../img/green2.png");
}
.item3{
   /* background-color: var(--main-color-dark-violet);*/
}
.login{
    color: #ffffff;
    font-size: 14px;
}

@media screen and  (max-width : 960px) {
    .block{
        width: 100%;
    }
}
@media screen and  (max-width : 660px) {
    .block{
        font-size: 11px;
    }

}

@media screen and  (max-width : 520px) {
    .block{
        display: none;
    }

}