
.btn{
    background: url("../../img/green.png");
    color:white;
    font-size: 12px;
    text-align: center;
    font-weight: 800;
    padding: 6px 12px;
    border-radius: 3px;
    text-transform: uppercase;
    cursor: pointer;
    border: none;
    outline: none;
    transition: background 0.7s ease;
}
.btn:hover{
    background-image: url("../../img/fon_hover_btn.png");
    color: var(--main-color-text);
}

.btn:focus{
    outline: none;
}
@media screen and  (max-width : 600px) {
    .btn{
        font-size: 10px;
        padding: 4px 9px;
    }

}