:root{
  --main-color-light-violet: #918DB0;
  --main-color-pastel-magenta: #876E8C;
  --main-color-dark-violet: #615C90;
  --main-color-text:#1D1628;
  --main-color-hover:#438008;
  --main-bgcolor: rgba(255,255,255,.85);
}


html, body{
  width: 100%;
  min-width: 100%;
  background: url("./img/1255048426_6919854_3118981e6b_o.jpg") no-repeat fixed;
  background-size: 100% 100%;
}
.App {
  width: 100%;
  color: var(--main-color-text);
  margin: 0 auto;
  font-family: " Raleway-Light", sans-serif ;
}
.content {
  width: 95%;
  max-width: 1280px;
  margin: 0 auto;
  height: auto;
background-color: var(--main-bgcolor);
}
h1,h2,h3,h4, h5{
  color: var(--main-color-text) !important;
  font-weight: 800!important;
}
.footer{
  background-image: url("./img/footer.png")!important;
  color: white!important;
  font-weight: 900!important;
}